<template>
  <div class="zem-dropdown">
    <ZemDropdownList
      class="zem-dropdown__list"
      :dropdownValues="regionValues"
      :isOpen="isOpenRegion"
      @onOpen="openList"
    >
      
    </ZemDropdownList>
    <ZemDropdownList
      class="zem-dropdown__list"
      :dropdownValues="statusValues"
      :isOpen="isOpenStatus"
      @onOpen="openList"
    >
        
    </ZemDropdownList>
  </div>
</template>

<script>
import ZemDropdownList from "./ui/DropdownList";

export default {
  name: "DropdownFilterExchangeForm",
  data() {
    return {
      regionValues: [
        "  ",
        " ",
        " ",
        " ",
        " !",
        "  ",
        " ",
        " ",
        " ",
        " ",
        " ",
        " ",
        " ",
        " ",
        " ",
        " ",
      ],
      statusValues: ["", "", "", "any else"],
      isOpenRegion: false,
      isOpenStatus: false,
    };
  },
  components: {
    ZemDropdownList,
  },
  methods: {
    openList(event) {
      switch (event.target.innerText.trim()) {
        case "":
          this.isOpenRegion = !this.isOpenRegion;
          break;
        case "  ":
          this.isOpenStatus = !this.isOpenStatus;
          break;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.zem-dropdown {
  display: flex;
  flex-direction: column;
}
</style>